<template>
  <div class="resume-share">
    <img :src="qrUrl" />
  </div>  
</template>


<script>
import qr from "qr-image";
export default {
  name: "share",
  data() {
    return {
      qrUrl: ""
    };
  },
  mounted() {
    const code = qr.imageSync(location.href, {
      parse_url: location.href,
      type: "png"
    });
    const url = "data:image/png;base64," + code.toString("base64");
    this.qrUrl = url;
  }
};
</script>




